.brand{
    font-family: Beth Ellen;
}

.typer{
    font-family: Finger Paint;
    font-size: 3em;
}
.welcome{
    font-family: Architects Daughter;
    font-size: 4em;

}
.intro{
    font-family: Barrio;
    font-size: 5em;
}
.navbar-dark {
    color:floralwhite;
    background-color:#000000;
}
.jumbotron {
    padding:70px 30px 70px 30px;
    margin:0px auto;
    background: none;
    color:black;
}


.navItem{
    margin: 5px;
    margin-right: 5px;
}

.aboutme{
    text-align: center;
    font-family: Delius Swash Caps;
    font-size: 4em;
    font-weight: 1000;
}
.aboutDescription{
    text-align: center;
    font-family: Handlee;
    font-weight: 700;
}
.backAbout{
    color:black;
}
.aboutLink{
    text-decoration: none;
    color: black;
}
.aboutLink:hover{
    color: rgb(230, 211, 174);
}

.transition { transition: .3s cubic-bezier(.3, 0, 0, 1.3) }
.card {
    background-color: #fff;
    box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.3);
    height: 300px;
    margin: auto;
    overflow: hidden;
    width: 300px;
}
.card:hover {
    height: 450px;
    width: 300px;
}
.card:hover .cta-container {
    display: inline;
    margin-top: 380px;
}
.card:hover .card_circle {
    background-size: cover;
    border-radius: 0;
    margin-top: -130px;
}
.card:hover .h2 {
    background: #3487f7;
    color: #fff;
    margin-top: 100px;
    padding: 5px;
}
.card:hover .h2 small { color: #fff }
.card:hover .p2 { margin-top: 300px }
.card_circle {
    background-color: #3487f7;
    background-size: cover;
    border-radius: 50%;
    height: 400px;
    margin-left: -75px;
    margin-top: -270px;
    position: absolute;
    width: 450px;
}
.card_circle_img_one{
    background: url('Images/mathWebsite.jpg') no-repeat center bottom;
}
.card_circle_img_two{
    background: url('Images/mathGame.jpg') no-repeat center bottom;
}
.card_circle_img_three{
    background: url('Images/hotelManagement.jpg') no-repeat center bottom;
}
.card_circle_img_four{
    background: url('Images/resourceAllocator.jpg') no-repeat center bottom;
}
.card_circle_img_five{
    background: url('Images/restrauntWebsite.jpg') no-repeat center bottom;
}
.h2 {
    color: #3487f7;
    font-family: 'Raleway', sans-serif;
    font-size: 24px;
    font-weight: 200;
    margin-top: 150px;
    position: absolute;
    text-align: center;
    width: 100%;
    z-index: 9999;
}
.p2 {
    color: rgba(0,0,0,.6);
    font-family: 'Raleway', sans-serif;
    font-size: 100%;
    font-weight: normal;
    margin-top: 200px;
    position: absolute;
    text-align: center;
    z-index: 9999;
}
.cta-container {
    display: none;
    margin-top: 320px;
    position: absolute;
    text-align: center;
    width: 100%;
    z-index: 9999;
}
.cta {

    background-clip: padding-box;
    border: 2px solid #3487f7;
    border-radius: 2px;
    color: #3487f7;
    display: inline-block;
    font-family: 'Raleway', sans-serif;
    font-size: 17px;
    font-weight: 400;
    height: 36px;
    letter-spacing: 0.5px;
    line-height: 36px;
    margin-bottom: 15px;
    padding: 0 2rem;
    text-decoration: none;
    text-transform: uppercase;
    transition: 0.2s ease-out;
}
.cta:hover {
    background-color: #3487f7;
    box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    color: #fff;
    text-decoration: none;
}

i:hover{
    color: #fff;
}
.footer{
    background-color: #D1C4E9;
    margin:0px auto;
    padding: 20px 0px 20px 0px;
    opacity: 0.85;
}
.projectHeading{
    font-family: Boogaloo;
    font-size: 4em;
    font-weight: 1000;
    color: floralwhite;
}
.ending{
    font-family: Acme;
    font-size: 19px;
}

.footerHead{
    font-family:  Baumans;
    font-size: 2em;
    font-weight: 1000;
}

.chartHead{
    font-family: Carter One;
    font-size: 4em;
    font-weight: 1000;
}